<template>
  <div class="contnet">
    <section class="cardContent">
      <div class="top">
        <p>{{detail.v.name}}</p>
        <p>{{detail.v.state == 1 ? '已结束':detail.v.state == 2 ? '正在进行中':'已取消'}}</p>
      </div>
      <div class="vipContnet">
        <div>
          <span>客户手牌</span>
          <span>{{detail.v.number}}</span>
        </div>
        <div>
          <span>房间号</span>
          <span>{{detail.v.houseNum}}</span>
        </div>
        <div>
          <span>开始时间</span>
          <span>{{detail.v.starTime}}</span>
        </div>
        <div>
          <span>结束时间</span>
          <span>{{detail.v.beginTime}}</span>
        </div>
        <div>
          <span>服务时间</span>
          <span>1:00:00</span>
        </div>
        <div>暂无备注信息</div>
      </div>
    </section>
    <section class="cardContent">
      <div class="top">服务项目信息</div>
      <div class="service" v-for="(item,index) of detail.s" :key="index">
        <p class="Num">{{index + 1}}、</p>
        <p>{{item.name}}</p>
        <p>￥{{item.pay}}</p>
        <p>￥{{item.tc}}</p>
      </div>
      <div class="service allPrice">
        <p class="Num"></p>
        <p></p>
        <p>￥{{detail.allPay}}</p>
        <p>￥{{detail.allTc}}</p>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  created() {
    var allPay = 0;
    var allTc = 0;
    this.detail.s.forEach(e => {
      allPay += parseInt(e.pay);
      allTc += parseInt(e.tc);
    });
    this.detail.allPay = allPay;
    this.detail.allTc = allTc;
  },
  data() {
    return {
      detail: {
        v: {
          name: "经典套餐全身按摩（60分钟）",
          state: "1",
          number: "002",
          houseNum: "A021",
          starTime: "2019-12-12 10:00:00",
          beginTime: "2019-12-12 11:00:00"
        },
        s: [
          {
            name: "中式按摩",
            pay: "233",
            tc: "100"
          },
          {
            name: "中式按中式按摩",
            pay: "233",
            tc: "100"
          },
          {
            name: "中式中式按摩",
            pay: "233",
            tc: "100"
          }
        ]
      }
    };
  }
};
</script>

<style lang="scss" scoped>
.cardContent {
  background: $fc;
  margin: 0.8rem 0.8rem;
  padding-bottom: 0.5rem;
  border-radius: 0.5rem;
  font-size: 0.9rem;
  .top {
    @include fr(space-between);
    background-color: $TBlue;
    color: $fc;
    margin: 0rem;
    padding: 0.8rem 0.5rem;
    border-top-right-radius: 0.5rem;
    border-top-left-radius: 0.5rem;
  }
  .vipContnet {
    @include fc(center);
    padding: 0rem 0.5rem;
    font-size: 0.8rem;
    div {
      @include fr(space-between);
      @include wh(100%, 100%);
      padding: 0.8rem 0rem;
      border-bottom: 0.02rem solid $F5;
      span:first-child {
        color: $line;
      }
    }
    div:last-child {
      border-bottom: none;
      padding: 1rem 0rem;
      color: $line;
    }
    p {
      @include wh(50%, auto);
      text-align: left;
      margin-bottom: 0.5rem;
    }
  }
  .service {
    padding: 0.5rem 0.5rem 0rem 0.5rem;
    font-size: 0.8rem;
    @include fr(space-around);
    p {
      margin-bottom: 0.5rem;
      flex: 0.8;
    }
    .Num {
      flex: 0.2;
      font-weight: bold;
      color: #4162ff;
    }
    p:nth-child(2) {
      flex: 1;
      text-align: left;
      color: #4162ff;
    }
    p:nth-child(3) {
      color: $yellow;
    }
    p:last-child {
      color: $orange;
    }
  }
  .allPrice {
    border-top: 0.08rem dotted $Tline;
    margin-top: 0.5rem;
  }
}
</style>